<template>
  <b-row id="main">
    <b-col cols="12" @click="toAlarm">
      <b-alert
        variant="warning"
        show
        class="m-1 p-2 overflow-auto text-nowrap"
        >{{ Alarm.Alarm_msg }}</b-alert
      >
    </b-col>
    <b-col
      cols="12"
      md="6"
      v-for="(key, id) in filter_devType(dev)"
      :key="id"
      :id="key"
    >
      <b-card
        class="my-2"
        :title="lang.get(key)"
        sub-title=""
        @click="to(key)"
      >
        <b-card-body class="px-0">
          <ul class="p-0">
            <li
              v-for="(val, id) in devs[key]"
              :key="id"
              class="el-icon-link w-100"
              v-b-tooltip.hover
              :title="id"
            >
              <span class="text-center">{{ val.name || id }}</span>
              <div class=" float-right ">
                <div v-show="val.online">
                  <span class="mr-2"></span>
                  <b-spinner
                    variant="success"
                    type="grow"
                    label="Spinning"
                    small
                  ></b-spinner>
                </div>
                <div v-show="!val.online">
                  <span class="mr-2"></span>
                  <b-spinner variant="info" label="Spinning" small></b-spinner>
                </div>
              </div>
            </li>
          </ul>
        </b-card-body>
      </b-card>
    </b-col>
  </b-row>
</template>

<script>
import { io, socketConnect } from "../util/socket.io-client";
import { mapState, mapGetters } from "vuex";
import { Get_user_all_devs } from "../util/axios";
import { Loading, MessageBox, Message /* Notification */ } from "element-ui";
export default {
  data() {
    return {
      devs: {
        ups: {},
        ac: {},
        power: {},
        io: {},
        th: {}
      }
    };
  },
  computed: {
    ...mapState({ user: "user", token: "token", dev: "dev", Alarm: "Alarm" }),
    ...mapGetters({ lang: "lang" })
  },
  methods: {
    filter_devType(dev) {
      return Object.keys(dev);
    },
    to(key) {
      this.$router.push({ path: `/dev/${key}` });
    },
    toAlarm() {
      this.$router.push({ path: "/alarm" });
    }
  },
  mounted() {
    this.$nextTick()
      .then(() => {
        {
          /* 
      socket
      */
          socketConnect({ user: this.user, token: this.token });
          /* on devsinfo */
          io.on("newDevs", data => {
            this.$store.dispatch("newDevs", data);
            let {
              devType,
              devs: { devid, name, generateTime }
            } = data;
            let old = this.devs[devType][devid];
            this.devs[devType][devid] = Object.assign(old, {
              name,
              generateTime,
              online: true
            });
          });
          /* on stream */
          io.on("infoStream", data => {
            //console.log(data);
            this.$store.dispatch("infoStream", data);
          });
          /* on Alarm */
          io.on("Alarm", data => {
            this.$store.commit("setAlarm", data);
          });
        }
        {
          /* 
      
      */
          let main_page = Loading.service({ target: "#main" });
          Get_user_all_devs({
            user: this.user,
            token: this.token
          })
            .then(result => {
              main_page.close();
              let { code, msg, data } = result.data;
              if (code != 200) return;
              let dev = data[0].dev;
              for (let d of dev) {
                let { type, devid } = d;
                if (!this.devs[type][devid])
                  this.$set(this.devs[type], devid, {
                    devid,
                    online: false,
                    name,
                    generateTime: ""
                  });
              }
              console.log(msg);
              //Message(msg);
            })
            .catch(err => {
              Message(err);
              MessageBox.confirm(
                ``,
                "err"
              )
                .then(() => {
                  this.$router.push("/SetMain");
                })
                .catch(() => {
                  this.$router.push("/");
                });
            });
        }
        {
          /* 
      loginout
      */
          this.$on("loginOut", () => {
            console.log("loginOut");
            Message("loginout");
            io.emit("disconnect");
          });
        }
      })
      .catch(err => {
        MessageBox(err, "main error");
      });
  }
};
</script>

<style lang="scss" scoped></style>
